			uni.showLoading({
			  title: '加载中'
			});
			let timer = setTimeout(() => {
			  uni.hideLoading();
			}, 1000);
			//用户信息
			this.userDetails()
			//获取未读信息
			this.getNewsNum()
			//获取默认职位
			this.pupDefault()
			//获取职位列表
			this.pupList()
			
		}
		

  },
  components: {
	  mpvuePicker
  },
  props: {},
  methods: {
    showPicker() {
    			  this.$refs.mpvuePicker.show();
    			},
    			onConfirm(e) {
    				this.index = e.index
					// this.identityName = this.identList[this.index].label
					let id = this.identList[this.index].id
					switch(id){
							case 'SALES_DIRECTOR':
							 try{
								uni.setStorageSync('pupDefault','SALES_DIRECTOR')
							 }catch(e){
								//TODO handle the exception
								uni.showToast({
									title: '缓存出错',
									icon: 'none'
								});
								return
							 }
						    
						    this.userDetails()
						    this.arrList =arrListGM;
							this.isSpecial = true
						    this.isSeller = false;
							break;
					    case  'REGIONAL_MANAGER':
							try{
								uni.setStorageSync('pupDefault','REGIONAL_MANAGER')
								this.userDetails()
							}catch(e){
								//TODO handle the exception
								uni.showToast({
									title: '缓存出错',
									icon: 'none'
								});
								return
							}
							this.userDetails()
							setTimeout(function() {
								console.log(a[0][2].url)
								a[0][2].url= a[0][2].url+'?regionCode=' + _regionCode
								console.log(a[0][2].url)
								this.arrList = arrListRG;
							}, 500);
							
							this.isSpecial = true
						    this.isSeller = false;
							break;
						case 'BUY_DEPUTY':
						     try{
						     	uni.setStorageSync('pupDefault','BUY_DEPUTY')
						     }catch(e){
						     	//TODO handle the exception
						     	uni.showToast({
						     		title: '缓存出错',
						     		icon: 'none'
						     	});
						     	return
						     }
							this.userDetails()
						    this.arrList = arrListBuyB; 
						    this.isSeller = false;
							this.isSpecial = false
							break;
						case 'SELL_DEPUTY':
						    try{
						    	uni.setStorageSync('pupDefault','SELL_DEPUTY')
						    }catch(e){
						    	//TODO handle the exception
						    	uni.showToast({
						    		title: '缓存出错',
						    		icon: 'none'
						    	});
						    	return
						    }
							this.userDetails()
						    this.arrList = arrListSellB;
							this.isSpecial = false
						    this.isSeller = true; 
							break;
					}		
    			},
    			onChange(e) {
    			  this.index = e.index
    			},
    			onCancel(e) {
    			  console.log('onCancel')
    			  console.log(e);
    			},
    			selectChange(e){
    				this.index = e.detail.value
    			},
    			showSinglePicker() {
    				console.log(this.identList)
    				this.mode = 'selector'
